import React from 'react';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import ProductService from '../../services/productService';
import { USE_SANITY_IMG } from '../../services/constants';

type Params = {
  imageData: any;
  isFixed?: boolean;
  isSmall?: boolean;
}

const ImageWrapper = ({ imageData, isFixed = false, isSmall = false }: Params) => {
  if (USE_SANITY_IMG) {
    const image = getImage(imageData.asset);

    if (isFixed) {
      return <img src={ image?.images.fallback?.src } alt=""/>
    }

    return <GatsbyImage image={ image! } alt="" />
  }

  return <img src={ ProductService.getImageFilePath(imageData, isSmall ? '/small' : '') } alt=""/>
}

export default ImageWrapper;
